import { Injectable, ViewChild, ContentChild } from '@angular/core';
import * as moment from 'moment';
import { ModalController, IonContent } from '@ionic/angular';
import { SocialShareComponent } from '../components/social-share/social-share.component';

@Injectable({
  providedIn: 'root'
})
export class UtilService {




  constructor(
    private modalController : ModalController
  ) { 
  }


  setFormatDate(date){
    return moment(date).locale("es").calendar() +" " + moment(date).format('LT');;
  }

  delay(ms: number) {
    return new Promise( resolve => setTimeout(resolve, ms) );
  }

  formatHours(hours){
    let time = hours.split(':');

    return `${time[0]}:${time[1]}`;
  }

  setFormatMoment(val,format = 'DD-MMMM-YYYY'){
    return moment(val).locale('es').format(format);
  }

  getFormarHumans(val){
    return moment(val).locale('es').fromNow()
  }

  notFoundItemApi(error : String){
    return error.includes('No query results for ');
  }


  async share(){
    const modal = await this.modalController.create({
      component: SocialShareComponent,
      cssClass: 'backTransparent',
      backdropDismiss: true
    });
    return modal.present();
  }

  scrollTo(elementId: string) {
    let element = document.getElementById(elementId);
    if(element) {
      element.scrollIntoView({ 
        behavior: 'smooth',
        block: 'center',
        inline: 'center'
      })
    
      element.classList.add('parpadea')

      setTimeout(()=>{
        element.classList.remove('parpadea');
      },4000)
    
    }
}

 setScript(src , onload = null){

  for(var i=0,scs; scs=document.getElementsByTagName('script')[i];i++){
    if(scs.src.indexOf(src)>-1)
       return;
  }


  let script = document.createElement("script");
  script.type = "text/javascript";
  script.src = src;

  script.onload = () => {
    console.log('load script', src);
    if(onload)
      onload();
  }
  console.log('set script ', src)
  document.body.appendChild(script);
 }

}
