import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { environment } from 'src/environments/environment';
import { RequestService } from 'src/app/api/request.service';
import { Model } from 'src/app/api/models/model';

@Component({
  selector: 'app-social-share',
  templateUrl: './social-share.component.html',
  styleUrls: ['./social-share.component.scss'],
})
export class SocialShareComponent implements OnInit {


  public sharingList = [];
  public shareModel : Model;

  constructor(
    private modalCtrl : ModalController,
    public requestService : RequestService
  ) {
    console.log('url',this.getUrl());
    this.shareModel = new Model('Share',requestService);
   }

  ngOnInit() {
    this.test();
    this.sharingList =  [
      {
        title: 'Facebook',
        logo: 'assets/images/face.png',
        href: 'https://facebook.com/sharer/sharer.php?u=' + this.getUrl()
      },
      {
        title: 'Twitter',
        logo: 'assets/images/tw.png',
        href: 'https://twitter.com/intent/tweet/?text=Super%20fast%20and%20easy%20Social%20Media%20Sharing%20Buttons.%20No%20JavaScript.%20No%20tracking.&url=http://farm8.staticflickr.com/7027/6851755809_df5b2051c9_z.jpg'
      },
      {
        title: 'WhatsAap',
        logo: 'assets/images/whatsApp.png',
        href: `https://api.whatsapp.com/send?text=${this.getUrl()}`
      },
      {
        title: 'Email',
        logo: 'assets/socialShare/gmail.png',
        href: 'mailto:receipient@user.com?subject=Super%20fast%20and%20easy%20Social%20Media%20Sharing%20Buttons.%20No%20JavaScript.%20No%20tracking.&body=This is an email body shared directly from an Ionic app using URL or links. This can be used in any web app.'
      }
    ]
  }


  closeModal(){
    this.modalCtrl.dismiss();
  }

  getUrl(){
    let url = window.location.href.split('#')[1];
    return environment.server_url_base + '/redirect?url=' + url
  }

  test(){
    this.shareModel.api_function('createImageGame', {id:112}).subscribe(
      response => {
        console.log(response);
      }
    )

    
  }
}
